"use client";

import { useState, useRef } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { uploadMealPhoto } from "@/services/meals/uploadMealPhoto";
import { createClient } from "@/lib/supabase/client";

type Props = {
  open: boolean;
  onClose: () => void;
  onSubmit: (data: any) => void;
};

export default function MealModal({ open, onClose, onSubmit }: Props) {
  const fileInputRef = useRef<HTMLInputElement | null>(null);
  const [preview, setPreview] = useState<string | null>(null);
  const [file, setFile] = useState<File | null>(null);
  const [loading, setLoading] = useState(false);

  const handleSelectFile = (event: React.ChangeEvent<HTMLInputElement>) => {
    const f = event.target.files?.[0];
    if (!f) return;
    setFile(f);
    setPreview(URL.createObjectURL(f));
  };

  async function handleSubmit() {
    if (!file) return alert("Selecione uma imagem!");

    setLoading(true);
    const supabase = createClient();

    // 1. Verifica usu√°rio autenticado
    const {
      data: { user },
    } = await supabase.auth.getUser();

    if (!user) {
      setLoading(false);
      return alert("Fa√ßa login!");
    }

    // 2. Upload da imagem
    const { publicUrl } = await uploadMealPhoto(user.id, file);
    console.log("üì∏ Foto no Supabase:", publicUrl);

    // 3. Chamada √† API da IA (segura)
    const nutritionalData = await fetch("/api/meals/analyze", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ imageUrl: publicUrl }),
    }).then((r) => r.json());

    console.log("üçΩÔ∏è Dados retornados:", nutritionalData);

    setLoading(false);
    onSubmit?.(nutritionalData);
  }

  if (!open) return null;

  return (
    <AnimatePresence>
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
        className="fixed inset-0 bg-black/40 backdrop-blur-sm z-50 flex items-end"
      >
        <motion.div
          initial={{ y: 40, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          exit={{ y: 40, opacity: 0 }}
          className="bg-white w-full p-5 rounded-t-3xl shadow-xl"
        >
          <h2 className="text-lg font-semibold text-[#0A0A0A] mb-4">
            Registrar refei√ß√£o
          </h2>

          {/* Input oculto */}
          <input
            type="file"
            accept="image/*"
            ref={fileInputRef}
            className="hidden"
            onChange={handleSelectFile}
          />

          {!preview && (
            <button
              onClick={() => fileInputRef.current?.click()}
              className="w-full py-3 rounded-xl bg-[#00C974] text-white font-medium"
            >
              Selecionar foto
            </button>
          )}

          {preview && (
            <div className="flex flex-col gap-4">
              <img
                src={preview}
                className="w-full rounded-xl object-cover max-h-80"
                alt="preview"
              />

              <div className="flex gap-3">
                <button
                  className="flex-1 py-3 rounded-xl bg-gray-200 text-gray-700"
                  onClick={() => {
                    setPreview(null);
                    setFile(null);
                  }}
                >
                  Trocar foto
                </button>

                <button
                  className="flex-1 py-3 rounded-xl bg-[#00C974] text-white font-medium disabled:opacity-60"
                  onClick={handleSubmit}
                  disabled={loading}
                >
                  {loading ? "Processando..." : "Confirmar"}
                </button>
              </div>
            </div>
          )}

          <button
            className="mt-4 text-center w-full text-gray-600"
            onClick={onClose}
            disabled={loading}
          >
            Cancelar
          </button>
        </motion.div>
      </motion.div>
    </AnimatePresence>
  );
}
